.item-hide{
    display: none !important;
}

.signin-button-area a {
    display: block;
    margin-bottom: 10px;
    width: 300px;
    text-transform: uppercase;
    margin: 0 auto;
    margin-bottom: 10px;
    color: #2C608F;
    font-family: 'Titillium Web', sans-serif;
    font-weight: 500;
    font-size:22px;
    border: 2px solid #fff !important;
    padding: 10px 0px;
    cursor: pointer;
    background: #F0F0F0;
    text-align: center;
}

.signin-button-area a:hover{
    text-decoration: none;
}